import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import BenefitsItem from '../FormItem/benefitsItem.jsx'

export class StockForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "project": {   //
                "key": "", // 
                "title": "", //
                "category": "", //
                "type": "", //
                "detail": "",   //
                "accessory": {        //
                    "name": "",
                    "link": ""
                }
            },
            "financing": {  //
                "industry": "", //
                "aptitude": "", //
                "parentAptitued": "", //
                "region": [], //
                "previousProfits": "", //
                "recentProfits": "",
                "nextProfits": "",
                "number": "", //
                "deadline": "", //
                "maxRate": "" //
            },
            "mortgage": {       //
                "isNeed": "",    //
                "type": [],  //
                "name": "",  //
                "value": "",  //
                "rate": "" //
            },
            "guarantee": {
                "isNeed": "",    //
                "method": ""   //
            },
            "stock": {
                "type": "",  //:1   :2
                "property": "",  //:1   :2
                "number": "",  //
                "rate": "", //
                "estimate": {  //
                    "times": "",
                    "type": ""
                }
            },
            "target": {  //
                "name": "",
                "industry": "",
                "region": "",
                "value": "", //
                "previousProfits": "",
                "recentProfits": "",
                "nextProfits": ""
            },
            "assets": { //
                "region": "",
                "number": "",
                "value": ""
            },
            "audit": {
                "status": "",
                "comment": ""
            }
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        adminAjax({
            url: "/project/normal/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID},
            callback: function(data) {
                let newData = data;
                that.setState(Object.assign({loading: false}, newData));
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                adminAjax({
                    url: "/project/normal/audit",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        message.success("!", 1, function() {
                            hashHistory.push("/project/normal")
                        });
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("project.title", {
                            rules: [{require: true, message: "!"}],
                            initialValue: this.state.project.title
                        })(
                            <Input disabled={this.props.actionType=="detail" ? true : false} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                    >
                        <Select disabled defaultValue="2">
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                            <Option value="4"></Option>
                        </Select>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="/"
                    >
                        <RadioGroup disabled value={this.state.stock.type}>
                            <Radio value="1"></Radio>
                            <Radio value="2"></Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                    >
                        {getFieldDecorator("project.type", {
                            initialValue: this.state.project.type
                        })(
                            <Select disabled>
                                {
                                    this.state.assetsType.stock.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                    >
                        <Select disabled value={this.state.stock.property}>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "" : ""}
                    >
                        <div></div>
                    </FormItem>
                    {
                        this.state.stock.property == "1" ? <FormItem
                            required
                            {...formItemLayout}
                            label= ""
                        >
                            <Input disabled value={this.state.target.name} />
                        </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "" : ""}
                    >
                        {getFieldDecorator(this.state.stock.property == "1" ? "target.industry" : "financing.industry", {
                            initialValue: this.state.stock.property == "1" ? this.state.target.industry : this.state.financing.industry
                        })(
                            <Select disabled>
                                {
                                    this.state.industryList.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "" : ""}
                    >
                        <Cascader disabled
                                  options={this.state.cascaderRegion} value={this.state.stock.property == "1" ? this.state.target.region : this.state.financing.region}
                        />
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "" : ""}
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.property == "1" ? this.state.target.previousProfits : this.state.financing.previousProfits} />&nbsp;
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label={this.state.stock.property == "1" ? "" : ""}
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.number} />&nbsp;
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.rate} />&nbsp;%
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.property == "1" ? this.state.target.number : this.state.financing.number} />&nbsp;
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >

                        {getFieldDecorator("stock.estimate", {
                            initialValue: {
                                times: this.state.stock.estimate.times,
                                type: this.state.stock.estimate.type
                            }
                        })(<BenefitsItem actionType={this.props.actionType} />)}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.property == "1" ? this.state.target.recentProfits : this.state.financing.recentProfits} />&nbsp;
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >
                        <Input disabled style={{width: "95%"}} value={this.state.stock.property == "1" ? this.state.target.nextProfits: this.state.financing.nextProfits} />&nbsp;
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                        hasFeedback={this.props.actionType=="audit" ? true : false}
                    >
                        {getFieldDecorator("project.detail", {
                            rules: [{require: true, message: "!"}],
                            initialValue: this.state.project.detail
                        })(
                            <TextArea disabled={this.props.actionType == "detail" ? true : false} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >
                        <span className="ant-form-text"><a href={this.state.project.accessory.link}>{this.state.project.accessory.name}</a>
                        </span>
                    </FormItem>
                    {
                        this.props.actionType == "audit" ?
                            <FormItem
                                required
                                {...formItemLayout}
                                label=""
                                hasFeedback={this.props.actionType=="audit" ? true : false}
                            >
                                {getFieldDecorator("audit.status", {
                                    rules: [{required: true,message:"!"}],
                                    initialValue: this.state.audit.status
                                })(
                                    <RadioGroup>
                                        <Radio value="0"></Radio>
                                        <Radio value="1"></Radio>
                                    </RadioGroup>
                                )}
                            </FormItem> : ""
                    }
                    {
                        this.props.actionType == "audit" ?
                            <FormItem
                                {...formItemLayout}
                                label=""
                            >
                                {getFieldDecorator("audit.comment", {
                                    initialValue: this.state.audit.comment
                                })(
                                    <TextArea autosize={{ minRows: 3 }} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            this.props.actionType == "audit" ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}></Button> : ""
                        }
                        {
                            this.props.actionType == "audit" ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}></Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}></Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedStockForm = Form.create({})(StockForm);

module.exports = WrappedStockForm;


